import {createStore, combineReducers, applyMiddleware} from 'redux';
import thunk from 'redux-thunk'
import {composeWithDevTools} from 'redux-devtools-extension'
import { cartReducer } from './reducers/cartReducer';
import { productListReducers, productDetailsReducers } from './reducers/productReducers';
import { userLoginReducers, userRegisterReducers } from './reducers/userReducers';

const reducer = combineReducers({
    productList:productListReducers,
    productDetails: productDetailsReducers,
    cart:cartReducer,
    userLogin: userLoginReducers,
    userRegister: userRegisterReducers,
})

const cartItemsFromStorage = localStorage.getItem('cartItems')?
JSON.parse(localStorage.getItem('cartItems')): []

const userInformationStorage = localStorage.getItem('userInfo')?
    JSON.parse(localStorage.getItem('userInfo')): null

const initailState = {
    cart:{cartItems:cartItemsFromStorage},
    userLogin: {userInfo: userInformationStorage},
}
const middleware =[thunk]
const store = createStore(reducer,initailState,composeWithDevTools(applyMiddleware(...middleware)))

export default store;